.App{
    display: flex;
    align-items: center;
    justify-content: center;
}
.container{
    width: 30vw;
    display: flex;
    flex-direction: column;
}
.container > h3{
    text-align: center;
}
.person{
    display: flex;
    justify-content: space-between;
    padding: 1% 0;
}
.person-age, .person-name{
    padding-bottom: -2%;
}

.person > img {
    border-radius: 80px;
    width: 10vw;
    height: 10vw;
    object-fit: cover;
}
.container > button {
    width: 40%;
    margin: 2vh  5vh 2vh auto;
}